<template>
  <main>
    <div class="messages">
      <VueMessage
        type="error"
        :isVisible="state.formSubmitted && $v.invalid"
        size="big"
        text="Заполните все обязательные поля"
      />
      <VueMessage
        type="success"
        :isVisible="state.submitSuccess && state.formSubmitted && !$v.$invalid"
        size="big"
        text="Клиент успешно создан!"
      />
    </div>
    <!-- Форма -->
    <form
      v-if="!state.submitSuccess"
      @submit.prevent="submitForm"
      class="form"
      novalidate
    >
      <!--------------------- Личные данные --------------------->
      <div class="form__section">
        <div class="form__group">
          <!-- Фамилия -->
          <div class="form__field">
            <label for="surname" class="form__label">Фамилия*: </label>
            <input v-model="state.surname" name="surname" class="form__input" />
            <VueMessage
              type="error"
              :isVisible="
                state.formSubmitted &&
                !$v.surname.$pending &&
                $v.surname.$invalid
              "
              size="small"
              text="Это поле обязательно для заполнения"
            />
          </div>

          <!-- Имя -->
          <div class="form__field">
            <label for="name" class="form__label">Имя*: </label>
            <input v-model="state.name" name="name" class="form__input" />
            <VueMessage
              type="error"
              :isVisible="
                state.formSubmitted && !$v.name.$pending && $v.name.$invalid
              "
              size="small"
              text="Это поле обязательно для заполнения"
            />
          </div>
        </div>

        <div class="form__group">
          <!-- Отчество -->
          <div class="form__field">
            <label for="lastname" class="form__label">Отчество: </label>
            <input
              v-model="state.lastname"
              name="lastname"
              class="form__input"
            />
          </div>
          <!-- Телефон-->
          <div class="form__field">
            <label for="phone" class="form__label">Номер телефона*:</label>
            <input
              type="tel"
              id="phone"
              v-model="state.phone"
              pattern="/^\+7\d{10}$/"
              maxlength="12"
              class="form__input"
            />
            <VueMessage
              type="error"
              :isVisible="
                state.formSubmitted && !$v.phone.$pending && $v.phone.$invalid
              "
              size="small"
              text="Это поле обязательно для заполнения"
            />
          </div>
        </div>

        <div class="form__group">
          <!-- Дата рождения -->
          <div class="form__field">
            <label for="birth" class="form__label">Дата рождения*: </label>
            <div class="form__input-container">
              <input
                type="date"
                v-model="state.birth"
                name="birth"
                class="form__input"
              />
            </div>
            <VueMessage
              type="error"
              :isVisible="
                state.formSubmitted && !$v.birth.$pending && $v.birth.$invalid
              "
              size="small"
              text="Выберите дату рождения"
            />
          </div>
        </div>

        <!-- Врач -->
        <div class="form__field">
          <label for="activeDoctor" class="form__label">Лечащий врач: </label>
          <CustomSelect :options="['Иванов', 'Захаров', 'Чернышева']" />
        </div>

        <div class="form__group">
          <!-- Группа клиентов -->
          <div class="form__field">
            <label class="form__label">Группа клиентов*:</label>
            <div class="form__options-container">
              <label class="form__chkbox-label">
                <input
                  type="checkbox"
                  value="vip"
                  v-model="state.clientTypes.vip"
                />
                <span>VIP</span>
              </label>
              <label class="form__chkbox-label">
                <input
                  type="checkbox"
                  value="problematic"
                  v-model="state.clientTypes.problematic"
                />
                <span>Проблемные</span>
              </label>
              <label class="form__chkbox-label">
                <input
                  type="checkbox"
                  value="oms"
                  v-model="state.clientTypes.oms"
                />
                <span>ОМС</span>
              </label>
            </div>
            <VueMessage
              type="error"
              :isVisible="
                state.formSubmitted &&
                !$v.clientTypes.$pending &&
                $v.clientTypes.$invalid
              "
              size="small"
              text="Выберите хотя бы одну группу"
            />
          </div>

          <!-- Пол -->
          <div class="form__field">
            <label for="gender" class="form__label">Пол:</label>
            <div class="form__options-container">
              <div class="form__radio-field">
                <input
                  type="radio"
                  value="male"
                  v-model="state.gender"
                  name="gender"
                />
                <label for="male">Мужчина</label>
              </div>
              <div class="form__radio-field">
                <input
                  type="radio"
                  value="female"
                  v-model="state.gender"
                  name="gender"
                />
                <label for="female">Женщина</label>
              </div>
            </div>
          </div>
        </div>

        <!-- СМС -->
        <div class="form__field">
          <label class="form__chkbox-label">
            <input type="checkbox" value="no-sms" v-model="state.sms" />
            <span>Не отправлять смс</span>
          </label>
        </div>
      </div>

      <!--------------------- Адрес --------------------->
      <div class="form__section">
        <div class="form__group">
          <!-- Страна -->
          <div class="form__field">
            <label for="country" class="form__label">Страна: </label>
            <input
              v-model="state.country"
              name="surname"
              id="country"
              class="form__input"
            />
          </div>

          <!-- Область -->
          <div class="form__field">
            <label for="region" class="form__label">Область: </label>
            <input
              v-model="state.region"
              name="region"
              id="region"
              class="form__input"
            />
          </div>
        </div>

        <div class="form__group">
          <!-- Город -->
          <div class="form__field">
            <label for="city" class="form__label">Город*: </label>
            <input
              v-model="state.city"
              name="city"
              id="city"
              class="form__input"
            />
            <VueMessage
              type="error"
              :isVisible="
                state.formSubmitted && !$v.city.$pending && $v.city.$invalid
              "
              size="small"
              text="Это поле обязательно для заполнения"
            />
          </div>

          <!-- Улица -->
          <div class="form__field">
            <label for="street" class="form__label">Улица: </label>
            <input
              v-model="state.street"
              name="street"
              id="street"
              class="form__input"
            />
          </div>
        </div>

        <div class="form__group">
          <!-- Дом -->
          <div class="form__field">
            <label for="building" class="form__label">Дом: </label>
            <input
              v-model="state.building"
              name="building"
              id="building"
              class="form__input"
            />
          </div>
          <!-- Индекс -->
          <div class="form__field">
            <label for="zipcode" class="form__label">Индекс: </label>
            <input
              v-model="state.zipcode"
              name="zipcode"
              id="zipcode"
              class="form__input"
            />
          </div>
        </div>
      </div>

      <!--------------------- Паспорт --------------------->
      <div class="form__section">
        <div class="form__group">
          <!-- Тип документа -->
          <div class="form__field">
            <label for="document" class="form__label">Тип документа*: </label>
            <select
              v-model="state.document"
              name="document"
              id="document"
              class="form__select"
            >
              <option value="passport" class="form__option">Паспорт</option>
              <option value="birth-certificate" class="form__option">
                Свидетельство о рождении
              </option>
              <option value="drivers-licence" class="form__option">
                Вод. удостоверение
              </option>
            </select>
            <VueMessage
              type="error"
              :isVisible="
                state.formSubmitted &&
                !$v.document.$pending &&
                $v.document.$invalid
              "
              size="small"
              text="Выберите тип документа"
            />
          </div>
          <!-- дата выдачи -->
          <div class="form__field">
            <label for="document-date" class="form__label"
              >Дата выдачи*:
            </label>
            <input
              type="date"
              v-model="state.documentDate"
              name="document-date"
              id="document-date"
              class="form__input"
            />
            <VueMessage
              type="error"
              :isVisible="
                state.formSubmitted &&
                !$v.documentDate.$pending &&
                $v.documentDate.$invalid
              "
              size="small"
              text="Выберите дату выдачи"
            />
          </div>
        </div>

        <div class="form__group">
          <!-- Серия -->
          <div class="form__field">
            <label for="passport-series" class="form__label">Серия: </label>
            <input
              v-model="state.passportSeries"
              name="passport-series"
              id="passport-series"
              class="form__input"
            />
          </div>

          <!-- Номер -->
          <div class="form__field">
            <label for="passport-number" class="form__label">Номер: </label>
            <input
              v-model="state.passportNumber"
              name="passport-number"
              id="passport-number"
              class="form__input"
            />
          </div>
        </div>

        <!-- Кем выдан -->
        <div class="form__field">
          <label for="document-given-by" class="form__label">Кем выдан: </label>
          <input
            v-model="state.documentGivenBy"
            name="document-given-by"
            id="document-given-by"
            class="form__input"
          />
        </div>
      </div>

      <!-- Отправить -->
      <button type="submit" class="form__submit">Отправить</button>
    </form>
  </main>
</template>

<script>
import { reactive, toRefs } from "@vue/composition-api";
import { useVuelidate } from "@vuelidate/core";
import { required } from "@vuelidate/validators";
import {
  validateTextInput,
  validatePhoneNumber,
  validateClientTypes,
} from "../utils/validators";

import VueMessage from "./VueMessage.vue";
import CustomSelect from "./CustomSelect.vue";

export default {
  components: { VueMessage, CustomSelect },
  setup() {
    const initialState = reactive({
      name: "",
      surname: "",
      lastname: "",
      birth: "",
      gender: "",
      phone: "+7",
      clientTypes: { vip: false, problematic: false, oms: false },
      activeDoctor: "",
      sms: "",
      zipcode: "",
      country: "",
      region: "",
      city: "",
      street: "",
      building: "",
      document: "",
      passportSeries: "",
      passportNumber: "",
      documentGivenBy: "",
      documentDate: "",
      formSubmitted: false,
      submitSuccess: false,
    });

    const state = toRefs(initialState);

    const ruleKeys = [
      "name",
      "surname",
      "lastname",
      "birth",
      "gender",
      "phone",
      "clientTypes",
      "activeDoctor",
      "sms",
      "zipcode",
      "country",
      "region",
      "city",
      "street",
      "building",
      "document",
      "passportSeries",
      "passportNumber",
      "documentGivenBy",
      "documentDate",
    ];

    const rules = ruleKeys.reduce((acc, key) => {
      acc[key] = required;
      return acc;
    }, {});

    rules.birth = { required };
    rules.phone = { required: validatePhoneNumber };
    rules.clientTypes = { required: validateClientTypes };
    rules.city = { required: validateTextInput };
    rules.document = { required };
    rules.documentDate = { required };

    const $v = useVuelidate(rules, state);

    const submitForm = async () => {
      initialState.formSubmitted = true;

      if ($v.value.$invalid) {
        return;
      }

      try {
        // Send the validated form to the server to create a document for the client
        initialState.submitSuccess = true;
      } catch (error) {
        console.error("Form submission error:", error);
      }
    };

    return { state: initialState, $v, submitForm };
  },
};
</script>

<style lang="scss">
@import "../styles/variables.scss";

main {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 50px;
  padding: 50px 0;

  @include maxMobileSmall {
    padding: 25px 5px;
  }
}

input,
textarea,
select {
  background-color: transparent;
  border-bottom: 1px solid $border-color;
  color: $text-color;
  padding: 5px 2px;
  transition: $transition;

  &:hover,
  &:focus {
    border-bottom: 1px solid $focus-color;
  }
}

input[type="checkbox"] {
  width: 18px;
  height: 18px;

  display: flex;
  align-items: center;
  justify-content: center;

  appearance: none;
  border: 2px solid $border-color;
  border-radius: 2px;

  &:checked {
    border: 2px solid $focus-color;
  }

  &:checked:before {
    content: "\2713";
    color: $focus-color;
    font-size: 12px;
  }
}

input[type="radio"] {
  appearance: none;
  display: inline-block;
  width: 18px;
  height: 18px;
  border: 2px solid $border-color;
  border-radius: 50%;
  position: relative;

  &:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: $focus-color;
    display: none;
  }

  &:checked {
    border: 2px solid $focus-color;
  }

  &:checked:before {
    display: flex;
  }
}

.form {
  @include glassmorphism;
  width: 100%;
  max-width: 700px;
  display: flex;
  flex-direction: column;
  gap: 50px;
  padding: 50px;

  &__section {
    display: flex;
    flex-direction: column;
    gap: 25px;
  }

  &__group {
    display: flex;
    flex-wrap: wrap;
    gap: 25px;

    @include maxMobileSmall {
      flex-direction: column;
    }
  }

  &__field {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__input-container {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__options-container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding-top: 5px;
  }

  &__radio-field {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 15px;

    @include maxMobileSmall {
      font-size: 12px;
    }
  }

  &__label {
    font-size: 16px;

    @include maxMobileSmall {
      font-size: 13px;
    }
  }

  &__chkbox-label {
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 15px;

    @include maxMobileSmall {
      font-size: 12px;
    }
  }

  &__input {
    width: 100%;
    font-size: 14px;

    @include maxMobileSmall {
      font-size: 12px;
    }
  }

  &__submit {
    @include glassmorphism;
    padding: 12px 16px;
    margin-top: 15px;
    transition: $transition;

    &:hover {
      opacity: 0.8;
    }

    @include maxMobileSmall {
      font-size: 13px;
      margin-top: 5px;
    }
  }
}
</style>
