<template>
  <p
    :class="[
      'message',
      `message--${type}`,
      `message--${size}`,
      `${isVisible ? 'message--visible' : ''}`,
    ]"
  >
    {{ text }}
  </p>
</template>

<script>
export default {
  props: {
    type: {
      type: String,
      default: "success", 
    },
    text: {
      type: String,
      required: true,
    },
    isVisible: {
      type: Boolean,
      default: true, 
    },
    size: {
      type: String,
      default: "small", 
    },
  },
};
</script>

<style lang="scss">

$errorColor: #0a0a0a;
$successColor: #009e60;

.message {
  visibility: hidden;

  &--error {
    color: $errorColor;
  }

  &--success {
    color: $successColor;
  }

  &--small {
    font-size: 12px;
  }

  &--big {
    font-size: 20px;
  }

  &--visible {
    visibility: visible;
  }
}
</style>
